'use client';

import { useState } from 'react';
import axios from 'axios';
// import Link from 'next/link'; // a ÌÉúÍ∑∏ ÏÇ¨Ïö©ÏùÑ ÏúÑÌï¥ Ï£ºÏÑù Ï≤òÎ¶¨

export default function LoginPage() {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  // üîê Ïä§ÎßàÌä∏ Í∞ÑÌé∏ Î°úÍ∑∏Ïù∏ Ìï∏Îì§Îü¨
  const handleLogin = async (role: 'GUEST' | 'HOST') => {
    setLoading(true);
    setError('');

    const credentials = {
      email: role === 'GUEST' ? 'guest@test.com' : 'host@test.com',
      password: 'password123',
      name: role === 'GUEST' ? 'ÌÖåÏä§Ìä∏ Í≤åÏä§Ìä∏' : 'ÌÖåÏä§Ìä∏ Ìò∏Ïä§Ìä∏',
      role: role,
    };

    try {
      let user;

      // 1. ÌöåÏõêÍ∞ÄÏûÖ ÏãúÎèÑ
      try {
        const signupRes = await axios.post('http://127.0.0.1:3001/api/users/signup', credentials);
        user = signupRes.data;
      } catch (err: any) {
        // 2. Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Í≥ÑÏ†ï(409 Conflict)Ïù¥ÎùºÎ©¥ -> Î°úÍ∑∏Ïù∏ ÏãúÎèÑ
        if (err.response && err.response.status === 409) {
          const loginRes = await axios.post('http://127.0.0.1:3001/api/users/login', {
            email: credentials.email,
            password: credentials.password,
          });
          user = loginRes.data;
        } else {
          throw err;
        }
      }

      // 3. Ïú†Ï†Ä Ï†ïÎ≥¥ Ï†ÄÏû• Î∞è Ïù¥Îèô
      if (user && user.id) {
        localStorage.setItem('user', JSON.stringify(user));
        window.location.href = '/';
      } else {
        throw new Error('Ïú†Ï†Ä Ï†ïÎ≥¥Î•º Î∞õÏïÑÏò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.');
      }

    } catch (err: any) {
      setError(err.response?.data?.message || 'Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
    } finally {
      setLoading(false);
    }
  };

  // Ïù¥Î©îÏùº/ÎπÑÎ∞ÄÎ≤àÌò∏ Î°úÍ∑∏Ïù∏ Ìï∏Îì§Îü¨ (ÏßÅÏ†ë ÏûÖÎ†•)
  const handleManualLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      const res = await axios.post('http://127.0.0.1:3001/api/users/login', {
        email,
        password,
      });

      const user = res.data;
      
      if (user && user.id) {
        localStorage.setItem('user', JSON.stringify(user));
        window.location.href = '/';
      } else {
        throw new Error('Ïú†Ï†Ä Ï†ïÎ≥¥Î•º Î∞õÏïÑÏò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.');
      }
    } catch (err: any) {
      setError(err.response?.data?.message || 'Ïù¥Î©îÏùº ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Ïò¨Î∞îÎ•¥ÏßÄ ÏïäÏäµÎãàÎã§.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
          CaravanApp Î°úÍ∑∏Ïù∏
        </h2>
        <p className="mt-2 text-center text-sm text-gray-600">
          Í∞ÑÌé∏ Î°úÍ∑∏Ïù∏
        </p>
      </div>

      <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
          
          {error && (
            <div className="mb-4 p-3 bg-red-50 text-red-500 text-sm rounded-md border border-red-200">
              {error}
            </div>
          )}

          <div className="space-y-6">


            {/* Ïù¥Î©îÏùº Î°úÍ∑∏Ïù∏ Ìèº */}
            <form onSubmit={handleManualLogin} className="space-y-4">
              <div>
                <label htmlFor="email" className="block text-sm font-medium text-gray-700">Ïù¥Î©îÏùº</label>
                <input
                  id="email"
                  type="email"
                  required
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>
              <div>
                <label htmlFor="password" className="block text-sm font-medium text-gray-700">ÎπÑÎ∞ÄÎ≤àÌò∏</label>
                <input
                  id="password"
                  type="password"
                  required
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>
              <button
                type="submit"
                disabled={loading}
                className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-gray-800 hover:bg-gray-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500"
              >
                Î°úÍ∑∏Ïù∏
              </button>
            </form>

            <div className="mt-6">
              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-gray-300" />
                </div>
                <div className="relative flex justify-center text-sm">
                  <span className="px-2 bg-white text-gray-500">Í≥ÑÏ†ïÏù¥ ÏóÜÏúºÏã†Í∞ÄÏöî?</span>
                </div>
              </div>
              <div className="mt-6 grid grid-cols-1 gap-3">
                <a
                  // ‚úÖ [ÏàòÏ†ïÎê®] /register ÎåÄÏã† /auth/registerÎ°ú Î≥ÄÍ≤Ω
                  href="/auth/register"
                  className="w-full flex items-center justify-center px-4 py-2 border border-gray-300 shadow-sm text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50"
                >
                  ÌöåÏõêÍ∞ÄÏûÖ ÌïòÍ∏∞
                </a>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  );
}